import React from 'react'
import styled from 'styled-components'
import { Images, Metrics } from '../Themes'
import { useHistory } from "react-router-dom";

const LogoArea = styled.div`
	height: 0px;
	z-index: 1000;
	position: relative;
	@media (max-width: ${Metrics.tablet}){
		//background: #FAFAFA;
		width: 100%;
		position: fixed;
		height: 55px;
		z-index: 100;
		top: 0;
	}
`

const Img = styled.img`
	position: relative;
	top: 20px;
	left: 16px;
	cursor: pointer;
	transition: .6s;
	@media (max-width: ${Metrics.tablet}){
		position: fixed;
		top: 20px;
		left: 20px;
		width: 100px;
		min-height: 25px;
		max-height: 35px;
    }
`

const Logo = () => {
	const history = useHistory();
	return(
		<LogoArea onClick={() => history.push('/')}>
			<Img src={`${Images.logo}`} />
		</LogoArea>
	)
}

export default Logo